@import "../../assets/styles/setup/variables";


.app-header {
    width: 1100px;
    margin-left: auto;
    margin-right: auto;
    padding-block-start: 73px;
    margin-block-end: 129px;

    .header-nav {
        >* {
            margin-inline-end: 40px;
            color: #FFFFFF;
        }

        >*:not(:first-child) {
            font-size: 18px;
            line-height: 28px;
            font-family: livvic-normal, sans-serif;
            font-weight: 600;
        }
    }

    .contact-us {
        button {
            width: 153px;
            height: 48px;
            background-color: transparent;
            border-radius: 30px;
            border: 2px solid #FFFFFF;
            color: #FFFFFF;
            font-family: Livvic;
            font-family: livvic-normal;
            font-weight: 600;
            font-size: 18px;
            line-height: 28px;
        }
    }
}