@import "./assets/styles/setup/variables";


.top-container {
    height: 700px;
    background-color: #2C6269;
    position: relative;

    .svg1 {
        position: absolute;
        width: 200px;
        height: 200px;
        left: -100px;
        top: calc(50% - 200px/2);
    }

    .svg2 {
        position: absolute;
        right: 165px;
        bottom: 0px;
    }

    .content-wrapper {
        width: 1100px;
        margin: 0 auto;

        .content1 {
            h1 {
                font-family: livvic-normal;
                font-style: normal;
                font-weight: bold;
                font-size: 100px;
                line-height: 100px;
                color: #FFFFFF;


                span {
                    color: #F67E7E;
                }
            }
        }

        .content2 {
            align-items: flex-end;

            p {
                font-family: livvic-normal;
                font-size: 18px;
                line-height: 28px;
                color: #FFFFFF;
            }
        }
    }
}

.middle-container {
    height: 700px;
    background-color: #002529;
    position: relative;
    padding: 140px 0px;

    .svg3 {
        position: absolute;
        right: -100px;
        bottom: 0px;
        mix-blend-mode: normal;
    }

    .main-wrapper {
        width: 1110px;
        height: 404px;
        margin: 0 auto;
        justify-content: space-between;

        h1 {
            margin-top: 58px;
            font-family: livvic-normal;
            font-style: normal;
            font-weight: bold;
            font-size: 48px;
            line-height: 48px;
            color: #FFFFFF;
            align-self: flex-start;
        }


        .content2 {
            align-self: flex-end;

            >*:not(:last-child) {

                margin-block-end: 32px;
            }

            .preview {
                width: 540px;
                height: 94px;

                img {
                    align-self: flex-start;
                }

                .text-wrapper {
                    .txt1 {

                        font-family: livvic-normal;
                        font-style: normal;
                        font-weight: bold;
                        font-size: 18px;
                        line-height: 28px;
                        color: #F67E7E;
                    }

                    .txt2 {
                        font-family: livvic-normal;
                        font-style: normal;
                        font-size: 15px;
                        line-height: 25px;
                        color: #FFFFFF;

                    }
                }
            }
        }
    }
}

.bottom-container {
    height: 700px;
    background-color: #004047;    
    position: relative;
    padding: 140px 0px;
}